'use strict';

/**
 * A fully-qualified name for an event type.
 *
 * Example: `'analytics.commons.engage.SaveOffer'`
 *
 * @typedef {String} SchemaKey
 */

/**
 * An object that represents an OWEN event and its details.
 *
 * Example: `'analytics.commons.engage.SaveOffer'`
 *
 * @typedef {Object} EventPayload
 */

var logError = require('./lib/util/log-error');
var validateEventProperties = require('./lib/validateEventProperties');

// We pull the committed packageVersion from the repo as a file because
// we don't want to unnecessarily force webpack configuration downstream,
// which would happen if we used a plugin to inject this into the built file.
var packageVersion = require('./data/packageVersion');
var trackOWENBrowserEvent = require('./lib/trackOWENBrowserEvent');
var viewUtils = require('./lib/util/view');
var uuidUtils = require('@rmn/uuid');
var SchemaKeys = require('./lib/shortSchemaKeys');
var owenSpecData = require('./data/owenSpecData.js');
var getEventSequence = require('./lib/util/sequence')();

/**
 * The primary OWEN module.
 * <br><br>
 * Note regarding loading a custom {@link OWENSpecData} with options.owenSpecData:
 * Because this is an edge case, no work has been done to make the original owenSpecData.json
 * file NOT be included in a webpacked version of the `overlord-js-sdk`. If you truly
 * need a slimmer consumable browser SDK, this module will need to be modified with separate
 * client and server-specific environment entry points.
 *
 * For specifics on what options can be passed to it, see {@link OWEN._init}.
 *
 * @namespace  OWEN
 * @example var OWEN = require( 'overlord-js-sdk' )( { options } );
 */
var OWEN = {};

/**
 * The SDK version of the `overlord-js-sdk`.
 * @type {String}
 */
OWEN.sdkVersion = packageVersion;

/**
 * An internal variable indicating whether this object has been initialized
 * for usage doing actual event emissions or not.
 * @type {Boolean}
 * @private
 */
OWEN._initialized = false;
OWEN._debug = false;

/**
 * The internal method used to actually initialize OWEN. The options passed to `overlord-js-sdk`
 * when it is required are passed through to this function. When being used in production, OWEN
 * should NOT be initialized more than once and will throw an error if it is initialized more
 * than once on the same page.
 *
 * Internally, OWEN is a **singleton**. *This method is not a function constructor*, meaning that
 * you do **not** use the `new` keyword when calling this method. It does the following:
 *
 * 1. It configures the singleton by calling `configure` with these options, which does more
 * thorough validation of the options that you passed.
 * 2. Generates a new viewInstanceUuid
 * 3. Gathers the old previousViewInstanceUuid if it is set in cookies in a browser environment
 * 4. Fires any deferred events found in localStorage
 * 5. If `doViewPageEvent` is truthy or undefined, fires a ViewPage event.
 *
 * @memberOf OWEN
 * @private
 * @param {Object} options An options object.
 * @param {String} options.host Required. The URL for the OWEN endpoint.
 * @param {String} options.eventPlatform Required. The platform this SDK will fire events from. This automatically
 *                                       sets the event.eventPlatform property.
 * @param {Boolean} [options.doViewPageEvent=true] Whether or not to fire a ViewPage event as soon as
 *                                                 this module is required. In browser environments, this
 *                                                 defaults to true. If you are in a server environment,
 *                                                 this has no effect and does not fire on in initialization.
 * @param {Function} [options.contextBuilder] A function that will automatically be used to create a boilerplate
 *                                             OWEN event object. OWEN will first create a basic context and pass
 *                                             it to this callback as the first argument, from which point you can
 *                                             mutate the original object. This is useful for creating a constant
 *                                             context for all events you fire to Overlord.
 * @param {Function} [options.trackFunction={@link module:lib/trackOWENBrowserEvent}] A function in charge of
 *                                          sending off the actual event to the OWEN endpoint. On the
 *                                          client side, this role is automatically fulfilled by the
 *                                          OWEN {@link lib/trackOWENBrowserEvent}. If you're in a server-side
 *                                          environment, you must supply your own implementation
 *                                          of trackFunction (and then please submit it as a pull request
 *                                          to this repository). See {@link module:lib/trackOWENBrowserEvent}.
 * @param {OWENSpecData} [options.owenSpecData] A custom {@link OWENSpecData} object.
 *                                              It's unlikely that you need to pass your
 *                                              own, but it exists as an option for
 *                                              convenience.
 * @return {OWEN} The OWEN singleton
 */
OWEN._init = function (options) {
    // If this is set to true, we skip everything, set a debug mode, and simply
    // return the object for testing.
    if (options.debug === true) {
        this._debug = true;
        return this;
    } else if (options.debug === false) {
        this._debug = false;
    }

    this.configure(options);

    if (OWEN._initialized && this._debug === false) {
        throw new Error(
            'OWEN has already been initialized. It should not be initialized ' +
            'more than once outside of debug mode.'
        );
    } else {
        OWEN._initialized = true;
    }

    // Always refresh the cached viewUtils on a new _init. Useful for testing.
    this.updateViewInstance();

    OWEN._fireDeferredEvents();

    // Fire off ViewPage events on init - defaults to true.
    if (options.doViewPageEvent || options.doViewPageEvent === undefined) {
        OWEN.fire.ViewPage(function (OWENData) {
            if (OWENData && this.ampClientId) {
                if (!OWENData.context) {
                    OWENData.context = {};
                }
                if (!OWENData.context.custom) {
                    OWENData.context.custom = {};
                }
                OWENData.context.custom.ampClientId = this.ampClientId;
            }
        }.bind(this), false);
    }

    return this;
};

/**
 * Configures the OWEN module with given options. Part of the initialization step,
 * but can be called separately.
 * See {@link owen.OWEN} for options.
 *
 * @method  configure
 * @memberOf OWEN
 * @see  {@link OWEN|OWEN}
 * @return {OWEN} The OWEN singleton
 * @static
 * @private
 */
OWEN.configure = function (options) {
    if (options.host) {
        this.host = options.host;
    } else {
        throw new Error(
            'host property required in initialization options. Add a \'host\' property. ' +
            'Examples: \'http://127.0.0.1:8000/\', \'http://a.rmntest.com/\''
        );
    }

    if (options.eventPlatform) {
        /**
         * The eventPlatform used as the event.eventPlatform property
         * on events fired.
         * @type {String}
         * @public
         */
        this.eventPlatform = options.eventPlatform;
    } else {
        throw new Error(
            'eventPlatform required in OWEN initialization options. Add an \'eventPlatform\' property. ' +
            'Examples: \'www\', \'mdot\', \'app\''
        );
    }

    if (options.eventSpecsData) {
        this.eventSpecsData = options.eventSpecsData;
    } else {
        this.eventSpecsData = owenSpecData;
    }

    /**
     * The version of the OWEN schema that the SDK tracks.
     * @type {String}
     * @public
     */
    this.owenVersion = this.eventSpecsData.metadata.owenSchemaVersion;

    if (options.trackFunction) {
        this.trackFunction = options.trackFunction;
    } else {
        this.trackFunction = trackOWENBrowserEvent;
    }

    if (options.contextBuilder) {
        this.contextBuilder = options.contextBuilder;
    }

    if (options.ampClientId) {
        this.ampClientId = options.ampClientId;
    }

    return this;
};

/**
 * An object containing metadata and an array of event specs.
 * @typedef {Object} OWENSpecData
 *
 * @property {Object} metadata An object for holding metadata.
 * @property {Array<EventSpec>} eventSpecs An array of EventSpecs.
 */

/**
 * A simple data object that specifies a title, an array of required top-level
 * event property keys, and an array of required top-level context property
 * keys. Used to create {@link ShallowEventSpec} objects.
 *
 * @typedef {Object} EventSpec
 */

/**
 *
 * @callback fireEvent
 * @param  {SchemaKey} schemaKey - The schema key for the event type to be fired.
 * @param  {Function} payloadTransformMethod A function that is passed the event payload
 *  for mutation. This is what you use to affect the OWEN event payload.
 * @param  {Boolean} deferEvent Whether or not the event should be stored to only fire on
 *  the next page load. This is for situations where a user can redirect away
 */

/**
 * Core utility method of the OWEN module which can be used to fire a payload to OWEN.
 * It goes through the following steps:
 *
 * 1. A core event payload is built, setting some core event properties:
 *     - `event.eventPlatform` (from initialization)
 *     - `event.eventInstanceUuid` (randomly generated base 32 uuid)
 *     - `event.eventTimestamp` (ISO format event timestamp)
 *     - `event.eventSequence` (sequence based on a given viewInstanceUuid)
 *     - `context.viewInstanceUuid` (randomly generated base 32 uuid)
 *     - `context.previousViewInstanceUuid` (viewInstanceUuid from the last page, if available)
 * 2. A contextBuilder callback you provide at `overlord-js-sdk` initialization that mutates the payload.
 *      This is intended to provide contextual information about the platform for every event.
 * 3. An optional callback you provide at the time of an event fire call that mutates the payload on an
 *      event-by-event basis. This is used to implement the event's specific details.
 * 3. The event payload's properties are validated according to the schema of the event.
 * 4. The event is fired or deferred to be fired on the next initialization of the OWEN module (AKA, during
 *      the next page view).
 *
 * Example usage:
 *
 * ```
 * OWEN.fireEvent( 'analytics.common.engage.AutoSuggestSearch', function ( OWENData ) {
 *     OWENData.context.partialSearchTerm = 'amazo';
 *     OWENData.event.eventTarget = 'amazon.com';
 * }, true ); // true to defer the event to next page view
 * ```
 *
 * @memberOf  OWEN
 * @method fireEvent
 * @public
 * @instance
 * @param  {SchemaKey} schemaKey           The schema key for the event type to be fired.
 * @param  {Function} payloadTransformMethod A function that is passed the event payload
 *                                           for mutation. This is what you use to affect
 *                                           the OWEN event payload.
 * @param  {Boolean} deferEvent            Whether or not the event should be stored
 *                                         to only fire on the next page load. This is
 *                                         for situations where a user can redirect away
 */
OWEN.fireEvent = function (schemaKey, payloadTransformMethod, deferEvent) {
    // Grab a specific eventSpec from the schemaKey
    var eventSpec = this.eventSpecsData.eventSpecs[ schemaKey ];

    // If the eventSpec couldn't be resolved, throw an error
    if (!eventSpec) {
        throw new Error(
            'schemaKey ' + schemaKey + ' not found in ' +
            'overlord-js-sdk\'s owenSpecData.'
        );
    }

    // Build the context for this method.
    var payload = this.tryToBuildPayload(this.eventPlatform, this.contextBuilder);

    if (payload) {
        // Enable optional transformation of the payload at this point
        if (typeof payloadTransformMethod === 'function') {
            payloadTransformMethod(payload);

        // Allow payloadTransformMethod to be optional
        } else if (typeof payloadTransformMethod === 'boolean' && !deferEvent) {
            deferEvent = payloadTransformMethod;
        } else if (payloadTransformMethod !== undefined) {
            throw new Error('payloadTransformMethod must be a function.');
        }

        // Finally validate the event. Throws an error if it doesn't pass.
        this._validateEventProperties(
            eventSpec.requiredEventProperties,
            eventSpec.requiredContextProperties,
            payload,
            schemaKey
        );

        // Check whether we need to defer or fire the event. If local storage isn't
        // allowed (see OWEN._localStorageAllowed), we can't possibly defer it so
        // just try and fire immediately and hope for the best.
        if (deferEvent && this._localStorageAllowed) {
            this._deferEvent(schemaKey, payload);
        } else {
            this.trackFunction({
                host: this.host,
                schemaKey: schemaKey,
                sdkVersion: this.sdkVersion,
                owenVersion: this.owenVersion,
                payload: payload
            });
        }
    }
};

/**
 * Gets the current event sequence number and increments the sequence number for a this.viewInstanceUuid.
 * @memberOf OWEN
 * @type {Number}
 * @public
 * @returns {Number} The event sequence
 */
OWEN.getEventSequence = function () {
    return getEventSequence(this.viewInstanceUuid);
};

/**
 * Resets the viewInstanceUuid and previousViewInstanceUuid, useful for single page apps that need to
 * fire more than one ViewPage event per load of the SDK
 * @memberOf OWEN
 * @public
 */
OWEN.updateViewInstance = function () {
    viewUtils.tryToRefresh();

    this.viewInstanceUuid = viewUtils.getViewInstanceUuid();
    this.previousViewInstanceUuid = viewUtils.getPreviousViewInstanceUuid() || undefined;
};

/**
 * Literally just a wrapper for stubbing out validateEventProperties.
 * @private
 */
OWEN._validateEventProperties = function (
    requiredEventProperties,
    requiredContextProperties,
    payload,
    schemaKey
) {
    validateEventProperties(
        requiredEventProperties,
        requiredContextProperties,
        payload,
        schemaKey
    );
};

/**
 * @private
 * @param  {SchemaKey} schemaKey
 * @param  {EventPayload} payload  An EventPayload.
 */
OWEN._deferEvent = function (schemaKey, payload) {
    // We may defer extra events before we get the chance to fire them out,
    // so we should store the deferred data as an array containing object literals.
    var deferredEvents = this._grabDeferredEvents();
    if (!deferredEvents || !Array.isArray(deferredEvents)) {
        deferredEvents = [];
    }

    deferredEvents.push({
        schemaKey: schemaKey,
        eventPayload: payload
    });

    this._setDeferredLocalStorage(deferredEvents);
};

/**
 * Builds the start of the payload using eventPlatform option and
 * the contextBuilder that was given at the time of overlord-js-sdk's
 * intialization. It is not intended for direct usage.
 *
 * It sets the following properties:
 *
 * `event.eventPlatform` - by argument
 * `event.eventInstanceUuid` - a randomly generated base 32 encoded uuid
 * `event.eventTimestamp` - the timestamp in ISO format (TODO: verify this works in IE8)
 * `context.viewInstanceUuid` - a randomly generated base 32 encoded uuid
 * `context.previousViewInstanceUuid` (if applicable) - the viewInstanceUuid from the
 *                                                      previous session, scraped from cookies
 *
 * and on `context`, whatever properties are set by the `contextBuilder`.
 *
 * @private
 * @param  {String} eventPlatform   The string representing the platform.
 * @param  {Function} contextBuilder The `contextBuilder` passed to OWEN._init.
 *                                    It is passed the payload as the first argument
 *                                    for mutation.
 * @return {Object}                 The resulting payload
 * @throws Will throw an error if `window.crypto`, `window.msCrypto`, or the nodejs crypto module are unavailable
 */
OWEN.buildPayload = function (eventPlatform, contextBuilder) {
    if (!eventPlatform) {
        throw new Error('An eventPlatform must be passed to buildPayload.');
    }

    var instanceUuid;
    try {
        instanceUuid = uuidUtils.randomBase32();
    } catch (err) {
        throw new Error('Failed to generate event instance uuid: ' + err.message);
    }

    var eventPayload = {
        event: {
            eventPlatform: eventPlatform,
            eventInstanceUuid: instanceUuid,
            eventTimestamp: (new Date()).toISOString(),
            eventSequence: getEventSequence(this.viewInstanceUuid)
        },
        context: {}
    };

    eventPayload.context.viewInstanceUuid = this.viewInstanceUuid;
    if (this.previousViewInstanceUuid) {
        eventPayload.context.previousViewInstanceUuid = this.previousViewInstanceUuid;
    }

    if (typeof contextBuilder === 'function') {
        contextBuilder(eventPayload.context);
    } else if (contextBuilder !== undefined) {
        throw new Error(
            'Found a stored contextBuilder that is not a function. ' +
            'Did you instantiate overlord-js-sdk correctly?'
        );
    }

    return eventPayload;
};

/**
 * Attempts to build the start of the payload using eventPlatform option and
 * the contextBuilder that was given at the time of overlord-js-sdk's
 * intialization. All errors encountered are caught and logged to the console.
 * It is not intended for direct usage.
 *
 * It sets the following properties:
 *
 * `event.eventPlatform` - by argument
 * `event.eventInstanceUuid` - a randomly generated base 32 encoded uuid
 * `event.eventTimestamp` - the timestamp in ISO format (TODO: verify this works in IE8)
 * `context.viewInstanceUuid` - a randomly generated base 32 encoded uuid
 * `context.previousViewInstanceUuid` (if applicable) - the viewInstanceUuid from the
 *                                                      previous session, scraped from cookies
 *
 * and on `context`, whatever properties are set by the `contextBuilder`.
 *
 * @private
 * @param  {String} eventPlatform   The string representing the platform.
 * @param  {Function} contextBuilder The `contextBuilder` passed to OWEN._init.
 *                                    It is passed the payload as the first argument
 *                                    for mutation.
 * @return {Object}                 The resulting payload, or `null` if unsuccessful
 */
OWEN.tryToBuildPayload = function (eventPlatform, contextBuilder) {
    if (!eventPlatform) {
        throw new Error('An eventPlatform must be passed to tryToBuildPayload.');
    }

    try {
        return this.buildPayload(eventPlatform, contextBuilder);
    } catch (err) {
        logError('Failed to build event payload: ' + err.message);
    }
    return null;
};

/**
 * Windows violently throws an exception if the LocalData directory on Windows
 * machines have a lower integrity setting than the application does. This breaks
 * JavaScript, so we need to catch the exception.
 *
 * See: https://msdn.microsoft.com/en-us/library/bb625957.aspx
 *
 * To get around this, we check if we can apply localStorage at all, and if
 * we don't get any errors, that means it worked great.
 *
 * @type {Boolean}
 */
OWEN._checkLocalStorageAllowed = function (win) {
    if (!win) {
        win = window;
    }
    try {
        win.localStorage.setItem('overlordJSSDKLocalStorageTest', '_');
        var result = win.localStorage.getItem('overlordJSSDKLocalStorageTest');
        if (result === '_') {
            return true;
        }
        return false;
    } catch (e) {
        return false;
    }
};

OWEN._localStorageAllowed = OWEN._checkLocalStorageAllowed();

/**
 * The key to use when looking for OWENDeferredEvents in localstorage/cookies.
 * @type {String}
 */
OWEN._localStorageOWENEventKey = 'OWENDeferredEvents';

OWEN._getDeferredLocalStorage = function () {
    return JSON.parse(window.localStorage.getItem(this._localStorageOWENEventKey));
};

OWEN._setDeferredLocalStorage = function (value) {
    window.localStorage.setItem(this._localStorageOWENEventKey, JSON.stringify(value));
};

OWEN._clearDeferredLocalStorage = function () {
    window.localStorage.removeItem(this._localStorageOWENEventKey);
};

/**
 * Grab any OWEN events that have been deferred from localStorage. Returns an array
 * of OWEN events, or null if no events are currently deferred.
 *
 * @return {Array|null} An array of OWEN event objects or null
 * @private
 */
OWEN._grabDeferredEvents = function () {
    var events;
    if (this._localStorageAllowed) {
        events = this._getDeferredLocalStorage();
        if (!Array.isArray(events)) {
            this._clearDeferredLocalStorage();
            return null;
        }
    } else {
        return null;
    }
    return events;
};

/**
 * Internal function that fires any deferred OWEN events currently in local storage.
 *
 * @memberOf OWEN
 * @function _fireDeferredEvents
 * @private
 */
OWEN._fireDeferredEvents = function () {
    var deferredEvents;
    if (this._localStorageAllowed) {
        deferredEvents = this._grabDeferredEvents();
    } else {
        return;
    }

    var self = this;
    if (deferredEvents) {
        deferredEvents.forEach(function (event) {
            if (event.schemaKey && event.eventPayload) {
                self.trackFunction({
                    schemaKey: event.schemaKey,
                    payload: event.eventPayload,
                    owenVersion: self.owenVersion,
                    sdkVersion: self.sdkVersion,
                    host: self.host
                });
            }
        });
        this._clearDeferredLocalStorage();
    }
};

/**
 * Object property on OWEN that provides convenient utility methods
 * to make sure that end-users cannot mess up the string passed to it.
 *
 * @memberOf OWEN
 * @type {Object}
 * @public
 * @static
 * @namespace OWEN.fire
 */
OWEN.fire = {};

/**
 * Registers an event helper method.
 *
 * @memberOf OWEN
 * @function registerEventHelper
 * @param {string} name - identifier for the helper method
 * @param {string} [schemaKey] - object path to the event spec within the OWEN schema;
 *  defaults to the auto-detected path ending in the helper name
 * @param {function} [helper=fireEvent] - custom event fire method; defaults to calling the
 *  `fireEvent` method if not provided
 * @public
 * @static
 */
OWEN.registerEventHelper = function (name, schemaKey, helper) {
    if (name === undefined) {
        throw new Error('Event name is required.');
    }

    // If the schema key is not provided, but a custom
    // helper function is provided, then fix the argument assignments
    if (typeof schemaKey === 'function') {
        helper = schemaKey;
        schemaKey = undefined;
    }

    // If a custom helper method is not provided,
    // default it to calling the `fireEvent` method
    if (typeof helper !== 'function') {
        helper = OWEN.fireEvent;
    }

    // The schema key defaults to the auto-detected event
    // schema matching the event name
    if (schemaKey === undefined) {
        schemaKey = SchemaKeys[ name ];
    }

    OWEN.fire[ name ] = helper.bind(OWEN, schemaKey);
};

// Pre-register some helper methods
[
    'Custom',
    'NavigationTiming',
    'RenderedAd',
    'RenderedImpression',
    'ResourceTiming',
    'RuntimeError',
    'ScrollPage',
    'UserTiming',
    'ViewPage',
    'ViewPrompt',
    'ViewableImpression',
    'ActiveExperiment',
    'AddComment',
    'AutoSuggestSearch',
    'ClickPage',
    'ClickPrompt',
    'EmailOffer',
    'FavoriteStore',
    'PrintOffer',
    'RequestOffer',
    'SaveOffer',
    'SavingsOffer',
    'Search',
    'ShareOffer',
    'Signin',
    'Signout',
    'Signup',
    'SubmitOffer',
    'SubscribeNotification',
    'TextOffer',
    'UnfavoriteStore',
    'UnsaveOffer',
    'UnsubscribeNotification',
    'VoteOffer'
].forEach(function (event) {
    OWEN.registerEventHelper(event);
});

module.exports = function (options) {
    return OWEN._init(options);
};
