'use strict';

/**
 * @module owen/lib/buildCompressedEventObjectForCollector
 */

/**
 * The version used by the Overlord collector endpoints.
 * This is a stable number and is unlikely to change
 * in the near future.
 * @type {String}
 */
var uriVersion = '2.0.0';

/**
 * An object in a specific compressed format that
 * endpoint collectors expect to receive.
 *
 * @typedef {Object} CompressedCollectorPayload
 *
 * @property {String} k The schema key of the event
 *                      'analytics.common.discover.ViewPage'
 * @property {String} s The version of the overlord-js-sdk
 *                      module, prepended by `j-`. Ex:
 *                      'j-1.3.2'
 * @property {Object} j The event payload.
 * @property {Object} v The OWEN version being tracked.
 * @property {String} u URIVersion - the version of URI that
 *                      collectors know how to parse.
 */

/**
 * OWEN-domain specific function that takes an object and formats it into a new
 * object with shortened keys that the OWEN collectors expect to receive.
 *
 * @static
 * @param  {Object} options An object containing the properties
 *                          for an event payload.
 * @param {String} options.schemaKey An event schema key
 * @param {String} options.sdkVersion The sdk version of this module
 * @param {String} options.owenVersion The version of OWEN schemas
 *                                     this module tracks
 * @param {String} options.payload A total event payload
 * @return {CompressedCollectorPayload}
 */
module.exports = function (options) {
    /**
     * @type {CompressedCollectorPayload}
     */
    return {
        k: options.schemaKey,
        s: 'j-' + options.sdkVersion,
        j: JSON.stringify(options.payload),
        v: options.owenVersion,
        u: uriVersion
    };
};
