'use strict';

/**
 * @module owen/lib/transports
 */

/**
 * Transports for sending OWEN events. Each function returns true if it was
 * successful in sending the event, and false otherwise.
 *
 * @type {{
 *    trySendBeacon: module.exports.trySendBeacon,
 *    tryImageRequest: module.exports.tryImageRequest,
 *    tryXhrRequest: module.exports.tryXhrRequest
 *  }}
 */
module.exports = {

    /**
     * Tries to send an event using the browser's sendBeacon functionality.
     *
     * @param event the overlord event to transport
     * @param options the options to event with
     * @return {boolean} whether the request was successful
     */
    trySendBeacon: function (event, options) {
        var success = false;

        if ('sendBeacon' in window.navigator) {
            success = window.navigator.sendBeacon(
                options.host + '/__wsm.gif', encodeURIComponent(JSON.stringify(event)));
        }

        return success;
    },

    /**
     * Tries to send an event using an Image pixel.
     *
     * @param event the overlord event to transport
     * @param options the options to even with
     * @param xhrSupported whether XHR is supported in the browser
     * @returns {boolean} whether the request was successful
     */
    tryImageRequest: function (event, options, xhrSupported) {
        var request = options.host + '/__wsm.gif?' +
            'k=' + event.k +
            '&s=' + event.s +
            '&j=' + encodeURIComponent(event.j) +
            '&v=' + event.v +
            '&u=' + event.u;

        if (request.length <= options.maxUriLength || !xhrSupported) {
            var image = new window.Image();
            image.src = request;
            return true;
        }

        return false;
    },

    /**
     * Tries to send an XMLHttpRequest with the Overlord Event.
     *
     * @param event the overlord event to transport
     * @param options the options to event with
     * @return {boolean} whether the request was successful
     */
    tryXhrRequest: function (event, options) {
        var XHR = new window.XMLHttpRequest();

        XHR.open('POST', options.host + '/__wsm.gif', true);
        XHR.withCredentials = true;
        XHR.setRequestHeader('Content-type', 'text/plain; charset=utf-8');
        XHR.send(encodeURIComponent(JSON.stringify(event)));

        return true;
    }
};
