'use strict';

/**
 * @module owen/lib/validateEventProperties
 */

var keys = require('lodash/keys');
var difference = require('lodash/difference');

/**
 * Validates a given object according to this schema. It throws a descriptive error
 * specifying what props are missing from the given payload. If no props are missing,
 * it returns undefined.
 *
 * @static
 * @param {Object} payload An OWEN event payload
 * @param {Array} requiredEventProperties   An array of strings that should be found as
 *                                          property keys on the "required" property of
 *                                          event payloads validated by this class
 * @param {Array} requiredContextProperties An array of strings that should be found as
 *                                          property keys on the "context" property of
 *                                          event payloads validated by this class
 */
module.exports = function (
    requiredEventProperties,
    requiredContextProperties,
    payload,
    schemaKey
) {
    var missingEventProps =
        difference(requiredEventProperties, keys(payload.event))
            .map(function (prop) {
                return 'event.' + prop;
            });
    var missingContextProps =
        difference(requiredContextProperties, keys(payload.context))
            .map(function (prop) {
                return 'context.' + prop;
            });

    if (missingEventProps.length > 0 || missingContextProps.length > 0) {
        throw new Error(
            ['This event (' + schemaKey + ') is missing the following properties:']
                .concat(missingEventProps, missingContextProps)
                .join('\n')
        );
    }
};
