'use strict';

/**
 * @module  owen/lib/shortSchemaKeys
 */

var specData = require('../data/owenSpecData');
var zipObject = require('lodash/zipObject');

var schemaKeys = Object.keys(specData.eventSpecs);

var schemaMap = zipObject(
    schemaKeys.map(function (schemaKey) {
        // This returns the last portion of the string after all
        // '.' in the string which delineate package segments.
        // Ex: 'analytics.common.discover.ViewPage' becomes 'ViewPage'
        return schemaKey.substr(schemaKey.lastIndexOf('.') + 1);
    }),
    schemaKeys
);

/**
 * A map of the shortened available SchemaKeys in the following format:
 *
 * ```
 * {
 *     "ViewPage": "analytics.common.discover.ViewPage",
 *     "AutoSuggestSearch": "analytics.common.engage.AutoSuggestSearch",
 *     ...
 * }
 * ```
 *
 * @type {Object}
 */
module.exports = schemaMap;
