/**
 * @module uuid
 */

var global = require( 'global/window' );
var base32 = require( 'base32.js' );

module.exports = {
	/**
	 * Return an array of 16 random bytes (0-255).
	 *
	 * Lifted from https://github.com/broofa/node-uuid
	 * This function is a reimplementation of node-uuid's internal lib/rng-browser.js,
	 * lib/rng.js, and v4.js modules, but without fallbacks to Math.random(), and not
	 * unparsing the returned value. Original implementation by CQMS.
	 *
	 * @method
	 * @license node-uuid license: https://github.com/broofa/node-uuid/blob/master/LICENSE.md
	 * @return {Array} An array of 16 bytes
	 * @throws Will throw an error if no crypto implementation is supported.
	 */
	random16Bytes: function () {
		var _rnds = new Uint8Array( 16 );
		var crypto = global.crypto || global.msCrypto;
		if ( crypto && crypto.getRandomValues ) {
			crypto.getRandomValues( _rnds );
		} else {
			getRandomValuesInNode( _rnds );
		}

		// Per 4.4, set bits for version and `clock_seq_hi_and_reserved`
		_rnds[ 6 ] = ( _rnds[ 6 ] & 0x0f ) | 0x40;
		_rnds[ 8 ] = ( _rnds[ 8 ] & 0x3f ) | 0x80;

		return _rnds;
	},

	/**
	 * Returns a unique 26-character RMN-style base-32 encoded uuid.
	 *
	 * @return {String} An RMN-style uuid
	 */
	randomBase32: function () {
		var base32uuid = base32.encode( this.random16Bytes() );

		return base32uuid;
	}
};

function getRandomValuesInNode ( buffer ) {
	var bytes;
	var crypto = require( 'crypto' );
	if ( crypto && typeof crypto.randomBytes === 'function' ) {
		bytes = crypto.randomBytes( buffer.length );
		buffer.set( bytes );
		return buffer;
	}
	throw new Error( 'No secure random number generator available.' ); 
}
