'use strict';

/**
 * @module owen/lib/remapPropertyKeys
 */

/**
 * Recursively transforms the keys in an object. It takes a map of keys describing how to
 * transform and an object to transform, and recurses through its keys, changing them
 * according to the keyMapping. If it encounters an unknown property, it throws an error.
 *
 * It has some domain-specific logic where it will refuse to transform an object (and its children)
 * whose key is "custom".
 *
 * @inner
 * @memberOf owen/lib
 * @param  {Object} keyMapping  A key mapping object used to transform the given object
 * @param  {Object} data        The object to transform
 * @param  {Object} propMapping A node in the total keyMapping. You do not need to pass this;
 *                              it is used by the algorithm internally.
 * @return {Object}             The transformed object
 */
function transformKeysRecursive(keyMapping, data, propMapping) {
    var transformed;
    if (data instanceof Array) {
        transformed = [];

        data.forEach(function (datum) {
            transformed.push(transformKeysRecursive(keyMapping, datum, propMapping));
        });

        return transformed;
    } else if (data instanceof Object) {
        transformed = {};

        for (var prop in data) {
            if (prop === 'custom') {
                if (data.hasOwnProperty(prop)) {
                    transformed[ propMapping[ prop ].s ] = data[ prop ]; // Don't transform
                }
            } else if (data.hasOwnProperty(prop)) {
                if (propMapping) {
                    if (propMapping[ prop ]) {
                        if (keyMapping[ prop ]) {
                            transformed[ propMapping[ prop ].s ] =
                                transformKeysRecursive(keyMapping, data[ prop ], keyMapping[ prop ].o);
                        } else {
                            transformed[ propMapping[ prop ].s ] =
                                transformKeysRecursive(keyMapping, data[ prop ], null);
                        }
                    } else {
                        throw new Error('Transformation Error: Unknown property ' + prop);
                    }
                } else if (keyMapping[ prop ]) {
                    transformed[ keyMapping[ prop ].s ] =
                        transformKeysRecursive(keyMapping, data[ prop ], keyMapping[ prop ].o);
                } else {
                    throw new Error('Transformation Error: Unknown property ' + prop);
                }
            }
        }
        return transformed;
    }
    return data;
}

/**
 * Transforms the given OWEN `payload` in such a way that the keys get transformed
 * into any shorter keys provided by the `keyMapping`.
 *
 * A key mapping looks like this:
 *
 * ```
 * {
 *     "englishReadableKey": {
 *         "s": "erk",
 *         "o": {
 *             // syntax recurses
 *         }
 *     }
 * }
 * ```
 *
 * "s" is the value that the key containing the object with the `"s"` property will be transformed
 * to (in this case, `"englishReadableKey"` -> `"erk"`), and `"o"` defines another definition like this.
 * The presence of an `"o"` means that the algorithm will then access "erk" as an object and in turn
 * transform its keys (and on and on, as far as you desire to nest the key mapping).
 *
 * @static
 * @param  {Object} keyMapping A mapping of keys to characters that is meant
 *                                  to be matched up to the payload.
 * @param  {Object} payload An OWEN-schema conforming event payload.
 * @return {Object}         A compressed OWEN-schema payload.
 */
module.exports = function (keyMapping, payload) {
    if (!keyMapping) {
        throw new Error('remapPropertyKeys requires a keyMapping to work!');
    }
    if (!payload) {
        throw new Error('remapPropertyKeys requires a payload to work!');
    }

    return transformKeysRecursive(keyMapping, payload, null);
};
