'use strict';

/**
 * Track and increment the sequence of Owen events for a given view instance UUID
 * @module owen/lib/util/squence
 * @return {getEventSequence~inner}
 */
function Sequence() {
    /**
	 * The default value to start a sequence with
	 */
    var DEFAULT_SEQUENCE = 0;

    /**
	 * Default Object to start a sequence map with
	 */
    var sequences = {};

    /**
	 * Map and increment event sequences for a given viewInstanceUuid.
	 * @function getEventSequence
	 * @inner
	 * @param  {String} viewInstanceId The viewInstance UUID
	 * @return {Number}                The sequence number for the given viewInstanceUuid
	 */
    return function getEventSequence(viewInstanceId) {
        if (typeof sequences[ viewInstanceId ] === 'undefined') {
            sequences[ viewInstanceId ] = DEFAULT_SEQUENCE;
        } else {
            sequences[ viewInstanceId ]++;
        }
        return sequences[ viewInstanceId ];
    };
}

module.exports = Sequence;
