'use strict';

/**
 * @module owen/lib/trackOWENBrowserEvent
 */

var buildCompressedEventObjectForCollector = require('./buildCompressedEventObjectForCollector');
var remapPropertyKeys = require('./remapPropertyKeys');
var compressKeyMapping = require('../data/compressMapping');
var transports = require('./transports');

/**
 * Detects whether we can emit events via XHR
 * @return {boolean} whether XHR is supported in the browser;
 */
function xhrPresent() {
    if ('XMLHttpRequest' in window) {
        var XHR = new window.XMLHttpRequest();
        return 'withCredentials' in XHR;
    }

    return false;
}

/**
 * Emits an event to the given OWEN endpoint, expecting to receive a standard OWEN event
 * payload. It emits the event by using `window.navigator.sendBeacon` or an `XMLHttpRequest`,
 * depending on browser support. In the event that `window.navigator.sendBeacon` fails to send
 * the event, fallback to using an `XMLHttpRequest`
 *
 * <h3>Creating your own tracker</h3>
 *
 * Creating a tracker is simple: simply create a function that accepts the same parameters
 * as `lib/trackOWENBrowserEvent` does. Pass this to [OWEN]{@link owen/owen} on instantiation as
 * the `tracker` parameter. You do not have to implement the `options` parameter unless you
 * require it.
 *
 * @static
 * @param {Object} options
 * @param {String} options.host A string representing the full URL of the endpoint this
 *                              tracker will emit events to.
 * @param {String} options.schemaKey The fully qualified key of an OWEN event.
 *                                   Example: 'analytics.common.discover.RenderedImpression'
 * @param  {Object} options.payload An OWEN schema-conforming event. trackOWENBrowserEvent()
 *                                  expects this event to be well-formed, and will not throw errors
 *                                  if it is not. It is inadvisable to use this method manually.
 * @param {String} options.sdkVersion A string representing overlord-js-sdk's version.
 * @param {String} options.owenVersion A string representing the owen-schemas version this
 *                                     module is tracking.
 * @param {Number} [options.maxUriLength=2000] The maximum length of the URI request to make in
 *                                             browsers that do not support window.navigator.sendBeacon.
 *                                             Defaults to 2000.
 */
module.exports = function trackOWENBrowserEvent(options) {
    if (!options.host) {
        throw new Error(
            'You must specify a `host` property on the options object  when calling trackOWENBrowserEvent.'
        );
    }
    if (!options.sdkVersion) {
        throw new Error(
            'You must specify a `sdkVersion` property on the options object  when calling trackOWENBrowserEvent.'
        );
    }
    if (!options.schemaKey) {
        throw new Error(
            'You must specify a `schemaKey` property on the options object  when calling trackOWENBrowserEvent.'
        );
    }
    if (!options.payload) {
        throw new Error(
            'You must specify a `payload` property on the options object  when calling trackOWENBrowserEvent.'
        );
    }
    if (!options.owenVersion) {
        throw new Error(
            'You must specify a `owenVersion` property on the options object when calling trackOWENBrowserEvent.'
        );
    }
    if (!options.maxUriLength) {
        options.maxUriLength = 2000;
    }

    // Map the long keys to shorter keys to reduce payload size
    options.payload = remapPropertyKeys(compressKeyMapping, options.payload);

    var event = buildCompressedEventObjectForCollector(options);

    if (!transports.trySendBeacon(event, options)) {
        if (!transports.tryImageRequest(event, options, xhrPresent())) {
            if (!transports.tryXhrRequest(event, options)) {
                throw new Error('Unable to send OWEN event. No supported transport mechanism.');
            }
        }
    }
};
