'use strict';

var uuidUtils = require('@rmn/uuid');
var cookies = require('cookies-js');
var logError = require('./log-error');

/**
 * @module  owen/lib/util/view
 * @type {Object}
 */
var ViewPageUtils = {
    PREVIOUS_VIEW_INSTANCE_UUID: null,
    VIEW_INSTANCE_UUID: null,

    /**
     * Retrieves the current internal VIEW_INSTANCE_UUID if it exists, otherwise
     * this calls {refresh} to generate a new one. If ViewPageUtils is in a browser
     * environment, it does nothing.
     *
     * @static
     * @method getViewInstanceUuid
     * @returns {String} The viewInstanceUuid
     */
    getViewInstanceUuid: function () {
        if (ViewPageUtils.VIEW_INSTANCE_UUID === null || ViewPageUtils.VIEW_INSTANCE_UUID === undefined) {
            ViewPageUtils.tryToRefresh();
        }
        return ViewPageUtils.VIEW_INSTANCE_UUID;
    },

    /**
     * Retrieves the current internal PREVIOUS_VIEW_INSTANCE_UUID. If a VIEW_INSTANCE_UUID has not
     * yet been generated with `refresh`, it calls `refresh` internally and returns null.
     *
     * @static
     * @method getPreviousViewInstanceUuid
     * @returns {String|null} The viewInstanceUuid from the last page view or null
     */
    getPreviousViewInstanceUuid: function () {
        if (ViewPageUtils.VIEW_INSTANCE_UUID === null || ViewPageUtils.VIEW_INSTANCE_UUID === undefined) {
            ViewPageUtils.tryToRefresh();
        }
        return ViewPageUtils.PREVIOUS_VIEW_INSTANCE_UUID;
    },

    /**
     * If in a browser environment, refreshes the internal VIEW_INSTANCE_UUID
     * with a new value, and sets the internal PREVIOUS_VIEW_INSTANCE_UUID
     * with the old value.
     *
     * The PREVIOUS_INSTANCE_UUID will not be updated for servers and browsers
     * that do not support cookies.
     *
     * @static
     * @method refresh
     * @throws Will throw an error if `window.crypto`, `window.msCrypto`, or the nodejs crypto module are unavailable
     */
    refresh: function () {
        var uuid = uuidUtils.randomBase32();
        ViewPageUtils.VIEW_INSTANCE_UUID = uuid;

        // not a browser or no cookie support
        if (!cookies.enabled) {
            return;
        }

        ViewPageUtils.PREVIOUS_VIEW_INSTANCE_UUID = cookies.get('overlord_viewInstanceUuid') || null;

        var splits = window.location.hostname.split('.');

        // this handles localhost
        var cookieDomain = null;

        if (splits.length >= 3) {
            splits[0] = '';
            cookieDomain = splits.join('.');
        }

        var cookieOpts = {path: '/', domain: cookieDomain};
        cookies.expire('overlord_viewInstanceUuid', cookieOpts)
            .set('overlord_viewInstanceUuid', uuid, cookieOpts);
    },

    /**
     * If in a browser environment, attempts to refresh the internal VIEW_INSTANCE_UUID
     * with a new value, and sets the internal PREVIOUS_VIEW_INSTANCE_UUID
     * with the old value. All errors are caught and logged to the console.
     *
     * The PREVIOUS_INSTANCE_UUID will not be updated for servers and browsers
     * that do not support cookies.
     *
     * @static
     * @method refresh
     */
    tryToRefresh: function () {
        try {
            this.refresh();
        } catch (err) {
            logError('Failed to refresh view instance uuids: ' + err.message);
        }
    }
};

module.exports = ViewPageUtils;
